#root{
    display: flex;
    align-items: center;
    justify-content: center;
}

.container{
    background: #e2f3f5;
    border-radius: 30px;
    width: 100vh;
    margin-top: 50px;
    padding: 30px;
}

.title{
    font-family: cursive;
    font-size: 2.5rem;
    text-transform: uppercase;
    color: #0e153a;
}

form.form{
    position: relative;
    border-bottom: solid;
}

input.input ,select.input{
    width: 10em;
    margin-right: 15px;
    height: 45px;
    font-size: 20px;
    box-sizing: border-box;
    padding: 10px;
    border-radius: 20px;
    text-align: center;
    cursor: pointer;
}

input.button{
    width: 10em;
    margin-right: 15px;
    height: 45px;
    font-size: 20px;
    box-sizing: border-box;
    padding: 10px;
    border-radius: 20px;
    cursor: pointer;
    background-color: #22d1ee;
}

input.button:hover{
    background-color: #0e153a;
    font-weight: bolder;
    color: #fff;
}

textarea{
    width: 21em;
    height: 80px;
    font-size: 20px;
    box-sizing: border-box;
    padding: 10px;
    border-radius: 20px;
    margin: 15px auto;
}

input:focus, textarea:focus, select:focus{
    outline: none;
}

#list{
    width: 100%;
    margin-top: 20px;
    padding: 15px;
    
}

table ,.notes {
    border-bottom: 1px solid black;
    border-collapse: collapse;
}

.colHead{
    background-color: #0e153a;
    font-weight: bolder;
    color: #fff;
    padding: 5px;
    height: 38px;
}

.notes{
    display: none;
    overflow: hidden;
}

.notes.active{
    display: table-cell;
 }

tr.tableRow{
    text-align: center;
    font-size: 17px;
    color: #0e153a;
    cursor: pointer;
}


.listButton{
    width: 4rem;
    height: 45px;
    font-size: 18px;
    box-sizing: border-box;
    border-radius: 20px;
    cursor: pointer;
    background-color: #22d1ee;
    margin-left: 10px;
}

.listButton:hover{
    background-color: #0e153a;
    font-weight: bolder;
    color: #fff;
}

.editNote{
    margin-left: 52px;
}